<template>
  <div :class="[classnames, center ? 'center-text' : '', 'paragraph-container']">
    <h2 v-if="title" class="title">{{title}}</h2>
    <h3 v-if="subtitle" class="subtitle">{{subtitle}}</h3>
    <p v-if="paragraph" class="paragraph">
      {{paragraph}}
    </p>
  </div>
</template>

<script>
    export default {
      name: 'Paragraph',
      props: {
        title: String,
        subtitle: String,
        paragraph: String,
        classnames: {
          type: String,
          default: ""
        },
        center: Boolean
      }
    }
</script>

<style lang="scss" scoped>
.paragraph-container {
  .title {
    padding-top: 50px;
    padding-bottom: 30px;
    text-transform: uppercase;
    font-size: 32px;
  }
  .subtitle {
    font-size: 20px;
    font-weight: bold;
    padding-top: 20px;
    padding-bottom: 20px;
    text-transform: uppercase;
  }
  .paragraph {
    line-height: 2;
    text-align: left;
  }

  &.center-text {
    text-align: center;
    width: 100%;

    .title,
    .subtitle {
      margin: 0 auto;
      width: 80%;
    }
    .paragraph {
      width: 70%;
      margin: 0 auto;
    }
    @media screen and (max-width: 560px) {
      .title,
      .subtitle,
      .paragraph {
        width: 100%;
      }
    }
  }
  @media screen and (max-width: 900px) {
    padding: 0 32px;
  }

}
</style>



