<template>
  <div class="col-100">
    <div class="carousel_container">
      <swiper :options="swiperOption" ref="mySwiper" @someSwiperEvent="callback">
        <swiper-slide v-for="item in items" v-bind:key="item.img">
          <div class="container" :style="containerStyle">
            <img
              :data-src="item.img"
              :style="imgStyle"
              class="swiper-lazy"
              :alt="item.alt"
              v-viewer="expandableOptions"
            />
          </div>
        </swiper-slide>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper';

// https://github.com/surmon-china/vue-awesome-swiper/tree/master/examples
export default {
  name: 'Carousel',
  components: {
    swiper,
    swiperSlide
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    }
  },
  data() {
    return {
      swiperOption: {
        lazy: true,
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      expandableOptions: {
        button: true,
        navbar: false,
        title: false,
        toolbar: false,
        tooltip: true,
        movable: true,
        zoomable: true,
        rotatable: false,
        scalable: false,
        transition: false,
        fullscreen: true,
        keyboard: true,
      }
    }
  },
  methods: {
    callback: (e) => {
    }
  },
  props: {
    items: {
      type: Array,
    },
    height: {
      type: Number
    },
    containerStyle: {
      type: Object,
      default: {},
    },
    imgStyle: {
      type: Object,
      default: {},
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  height: 300px;
  overflow: hidden;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (min-width:1280px) {
  .carousel_container {
    max-width: 1280px;
    margin: 0 auto;
  }
}

</style>



