<template>
  <div>
    <Width100>
      <div class="text">
        <Paragraph
          v-bind:center="true"
          :title="title"
          :subtitle="subtitle"
          :paragraph="paragraph"
        />
      </div>
      </Width100>
      <Carousel
        v-bind:items="carouselItems"
        :containerStyle="carouselStyle"
        :imgStyle="imgStyle"
      />
      <LineBreak />
  </div>
</template>

<script>
import Carousel from './Carousel';
import Paragraph from './Paragraph';
import LineBreak from './LineBreak';
import Width100 from './layout/Width100';

export default {
  components: {
    Carousel,
    LineBreak,
    Paragraph,
    Width100,
  },
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    },
    paragraph: {
      type: String
    },
    carouselItems: {
      type: Array
    },
    carouselStyle: {
      type: Object
    },
    imgStyle: {
      type: Object
    },
  },

};
</script>

<style lang="scss" scoped>
.text {
margin: 0 auto;
width: 80%;
}

@media screen and (max-width: 560px) {
  .text {
    width: 100%;
  }
}
</style>



