<template>
  <div class="container">
    <Width100>
      <div class="links-container">
        <router-link class="next-project-button" :class="isPreviousButtonDisabled" :to="previousButton" v-on:click.native="trackRoute(-1)">
          <div class="text-container">
            <div class="title">
              {{previousTitle}}
            </div>
            <div class="direction_text">
              Previous
            </div>
          </div>
        </router-link>

        <router-link class="next-project-button" :to="nextButton" :class="isNextButtonDisabled" v-on:click.native="trackRoute(1)">
          <div class="text-container">
            <div class="title">
              {{nextTitle}}
            </div>
            <div class="direction_text">
              Next
            </div>
          </div>
        </router-link>
      </div>
    </Width100>
  </div>
</template>

<script>
import Width100 from './common/layout/Width100';


export default {
    name: 'NextProject',
    components: {
      Width100,
    },
    data() {
      return {
        projects: [
          "/sumi",
          "/ign",
          "/fitted",
          "/gocards",
        ],
        titles: [
          "Sumi",
          "Ign",
          "Fitted",
          "GoCards",
        ],
      };
    },
    computed: {
      previousTitle() {
        const prevIdx = this.currentIdx - 1;
        if (this.currentIdx === -1 || prevIdx < 0) {
          return "";
        }
        return this.titles[prevIdx];
      },
      previousButton() {
        const prevIdx = this.currentIdx - 1;
        if (this.currentIdx === -1 || prevIdx < 0) {
          return "#";
        }
        return this.projects[prevIdx];
      },
      nextButton() {
        const nextIdx = this.currentIdx + 1;
        if (this.currentIdx === -1 || nextIdx >= this.projects.length) {
          return "#";
        }
        return this.projects[nextIdx];
      },
      nextTitle() {
        const nextIdx = this.currentIdx + 1;
        if (this.currentIdx === -1 || nextIdx >= this.projects.length) {
          return "";
        }
        return this.titles[nextIdx];
      },
      isNextButtonDisabled() {
      const nextIdx = this.currentIdx + 1;
        if (this.currentIdx === -1 || nextIdx >= this.projects.length) {
          return "disabled";
        }
        return "";
      },
      isPreviousButtonDisabled() {
        const prevIdx = this.currentIdx - 1;
        if (this.currentIdx === -1 || prevIdx < 0) {
          return "disabled";
        }
        return "";
      },
    },
    props: {
      currentIdx: {
        type: Number,
        default: -1,
      },
    },
    methods: {
      trackRoute(direction) {
        this.$ga.event({
          eventCategory: 'footer',
          eventAction: 'click',
          eventLabel: 'next-project-router',
          eventValue: direction
        });
      }
    }
};
</script>

<style lang="scss" scoped>
.container {
  background: #e76d89;

  .links-container {
    color: white;
    text-align: center;
    display: inline-block;
    width: 100%;
    padding: 16px;
    display:flex;
    justify-content:space-between;

    .next-project-button {
      width: 100px;
      color: white;
      .text-container {
        width: 100%;
      }
      .title {
        width: 100%;
        line-height: 22px;
        height: 27px;
        font-size: 20px;
      }
      .direction_text {
        width: 100%;
        height: 27px;
        font-size: 20px;
      }
      &.disabled {
        color: #e3e3e3;
        pointer-events: none;
      }
    }
  }

}
</style>



