<template>
  <div :class="[classnames, 'container']">
    <slot></slot>
  </div>
</template>

<script>
    export default {
      name: 'Container',
      props: {
        classnames: {
          type: String,
          default: ""
        },
      }
    }
</script>

<style lang="scss" scoped>
.container {
  width: 70%;
  margin: 0 auto;
}
@media screen and (max-width: 560px) {
  .container {
    width: 100%;
  }
}
</style>



