<template>
<div class="ordered_list">
  <h3 class="title" v-if="title" :style="titleStyles">{{title}}</h3>
  <ol>
    <li class="list_item" v-for="item in items">
      {{item}}
    </li>
  </ol>
</div>
</template>

<script>
export default {
  name: 'OrderedList',
  props: {
    items: {
      type: Array,
    },
    title: {
      type: String,
    },
    titleColor: {
      type: String,
    },
  },
  computed: {
    titleStyles: function() {
      return {
        color: this.titleColor ? this.titleColor: 'black',
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.ordered_list {
  padding: 32px;

}

.title {
  padding: 20px 0;
}
.list_item {
  margin-bottom: 16px;
  line-height: 2;
}

@media screen and (max-width: 900px) {
  .ordered_list {
    .title {
      text-align: center;
    }
  }
}
</style>



